* {
  box-sizing: border-box;
}

#parallaxintro .view {
  min-height: 75vh !important;
  height: unset !important;
}

.hero {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

nav.navbar.navbar-default {
  margin-bottom: 0;
}

.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

