ight:75vh;transform:translate3d(-100%, 0, 0);font-size:24px}.menu-branding{height:25vh;transform:translate3d(100%, 0, 0)}.about-info{grid-template-areas:'bioimage' 'bio' 'job1' 'job2' 'job3';grid-template-columns:1fr}.projects{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 500px){main{padding:2rem}main #home h1{margin-top:10vh}main .lg-heading{margin-top:1rem;font-size:5rem}.projects{grid-template-columns:1fr}}

